.page {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  flex-direction: column;
  width: 200px;
  margin: auto;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  display: block;
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 5px;
  margin-bottom: 16px;
  padding-right: 68px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__reviews-amount {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__star:not(:last-child) {
  margin-right: 4px;
}

.card__stars--active-4 .card__stars-star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 7px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  margin-bottom: 16px;
  padding: 11px 69px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__stars {
  justify-content: space-between;
  display: flex;
}

.card__stars-star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  display: inline-block;
}

.card__stars--active-4 .card__stars-star:nth-child(-n+4) {
